// URL()
let hotSearchUrl = '/ebapi/store_api/hot_search';
let indexUrl = '/ebapi/public_api/index';


// vmthisvmvuexuView
// https://uviewui.com/js/http.html#%E4%BD%95%E8%B0%93%E8%AF%B7%E6%B1%82%E6%8B%A6%E6%88%AA%EF%BC%9F
const install = (Vue, vm) => {
	// params
	let getSearch = (params = {}) => vm.$u.get(hotSearchUrl, {
		id: 2
	});
	
	// params
	let getInfo = (params = {}) => vm.$u.post(indexUrl, params);
	
	// vm.$u.api(vmthisthis.$u.api)
	vm.$u.api = {getSearch, getInfo};
}


export default {
	install,
	noNeedtokenUrl:['/area/list','/ordertime/list','/iventendcar/cars','/butt-auth',],
	tokenUrl:'https://chengym.com.cn/library/butt-auth',
	baseUrl: 'https://chengym.com.cn/library'
}