import api from './http.api.js'
async function doRequest (res,config){
	await uni.login({
		provider: 'weixin',
		success: async function(loginRes) {
			await uni.getUserInfo({
				provider: 'weixin',
				success: async function(infoRes) {
					let data = {
						code: loginRes.code,
						encryptedData: infoRes.encryptedData,
						iv: infoRes.iv,
						userInfo: infoRes.userInfo,
						rawData: infoRes.rawData,
						signature: infoRes.signature,
						channel: "wechat",
						inviter: uni.getStorageSync("inviter_user_id")
					}
						await uni.request({
							url: api.tokenUrl,
							method: 'POST',
							header: {
								"content-type": "application/json"
							},
							data: data,
							success: async ress => {
								if (ress && ress.statusCode === 200) {
									uni.setStorageSync("mini_car_token", ress.data.token)
									uni.setStorageSync("mini_car_userid", ress.data.userid)
									config.headers['Authorization']='Bearer '+ress.data.token
									await uni.request({
										url: config.url,
										method: config.method,
										header: config.header,
										data: config.data,
										success:async res2 =>{
											return res2.data
										}
									})
								} else {
									 
								}
							},
							fail: err => {
								console.log(err)
							}
						})
				}
			})
		}
	})
}
const install = (Vue, vm) => {
	Vue.prototype.$u.http.setConfig({
		baseUrl: api.baseUrl,
		// trueresponseresponse.data
		// truethis.$u.http.interceptor.response
		// originalData: true, 
		// content-type
		// header: {
		// 	'content-type': 'xxx'
		// }
	});
	
	// Token
	Vue.prototype.$u.http.interceptor.request = (config) => {
		// config.header.Token = 'xxxxxx';
		
		// vuextokenuViewvuexhttps://uviewui.com/components/globalVariable.html
		// config.header.token = vm.token;
		
		// uViewvuex$store.state
		// config.header.token = vm.$store.state.token;
		
		// tokenglobalDatagetApp().globalData
		// config.header.token = getApp().globalData.username;
		
		// tokenStorageStorage
		if(api.noNeedtokenUrl.indexOf(config.url)!=-1){
			
		}else{
			const token = uni.getStorageSync('mini_car_token');
			config.header.Authorization = 'Bearer ' + token;
		}
		
		return config; 
	}
	// 
	Vue.prototype.$u.http.interceptor.response =async (res,config) => {
		// originalDatatrue
		// res.statueCoderes.data.code
		if(res.code == 200) {
			// originalDatatruereturnthis.$u.postthen
			return res;  
		}else if(res.code ==401){
			return  await doRequest(res,config)
		} else return false;
	}
}



export default {
	install,
	doRequest
}